import React from 'react'
import CouponItem from './CouponItem'


class CouponList extends React.Component {
	render() {
		let coupons = this.props.coupons

		return (
			<div>
				
				{coupons.map((coupon) => (
					<span>{coupon.event}</span>
				))}
			</div>
		)
	}
}

export default CouponList